<style>
  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .ll-img-w {
    width: 60px;
    height: 60px;
  }
  .ll-img-w>img {
    width: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .c-g-sfw {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
</style>

<template>
  <div>
    <el-dialog :title="title" :visible.sync="isShow" width='720px'>
      <div class="search-form-wraper c-g-sfw">
        <el-form :model="searchContent" label-position="top">
          <el-form-item>
            <el-cascader
              placeholder="请选择分类"
              class="search-input"
              :options="categoryList"
              change-on-select
              size="small"
              clearable
              filterable
              @change="doCategorysChangeHandel"
              :props="cateDefaultProps"
              v-model="searchContent.categoryIdList">
            </el-cascader>
          </el-form-item>
          <el-form-item>
            <goods-select-for-dialog ref="GoodsSelectForDialog" @DoSelectGoodsHandel="DoSelectGoodsHandel"></goods-select-for-dialog>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
      
      <div>
        <el-table
          class="fixed-height"
          ref="multipleTable"
          v-loading="loading"
          :data="tableData"
          empty-text="没有查询到您想要的数据"
          style="width:100%;"
          height="300"
          size="small"
          @select="handleSelect"
          @select-all="handleSelectAll">
          <el-table-column
            type="selection"
            width="50">
          </el-table-column>
          <el-table-column min-width="226px" label="商品信息">
            <template slot-scope="scope">
              <div class="ll-p-m-w">
                <div class="ll-img-w">
                  <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                  <img v-else src="/shop/static/img_error.png" alt="">
                </div>
                <div class="ll-p-m-c-r">
                  <div>{{scope.row.goodsName || '-'}}</div>
                  <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
                  <div class="ll-erp-p-no">商品ID：{{scope.row.platformCityGoodsId || '--'}}</div>
                </div>
              </div>
            </template>     
          </el-table-column>
          <el-table-column min-width="100px" prop="presalePrice" label="预售价"></el-table-column>
          <el-table-column min-width="180px" label="预售时间">
            <template slot-scope="scope">
              <div>
                <p>{{scope.row.presaleStartDate}}-{{scope.row.presaleEndDate}}</p>
                <p>{{scope.row.presaleStartTime}}-{{scope.row.presaleEndTime}}</p>
              </div>
            </template>
          </el-table-column>
          <el-table-column min-width="100px" prop="presaleStatus" label="状态" :formatter="presaleStatusFormatter"></el-table-column>
        </el-table>
        <!-- 分页条 -->
        <el-pagination
          v-show="tableData.length>0"
          class="tc mt20"
          background
          @size-change="changeSize"
          @current-change="changeCurrent"
          :current-page.sync="pageNum"
          :page-sizes="[10, 50, 100, 150, 200]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="doConfirmHandel" size="mini" :disabled="isDisabled">导 入</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  import GoodsSelect from "@/components/Common/GoodsSelectByPid"
  const presaleGoodsMgt = {
    // 获取三方平台店内分类
    getCategoryTree: '/plateform/a/category/getCategoryTree',
    // 选择区域商品
    selectPageGoodsList: '/plateform/a/mall/pageTemp/selectPageGoodsList',
    // 区域商品选择-商品列表
    selectCityGoods: '/plateform/a/platformCityGoods/presale/selectCityGoods',
    // 商品推荐保存
    saveRecommend: '/plateform/a/mall/recommend/saveRecommend'
  }
  export default {
    data() {
      return {
        isDisabled: false,
        isShow: false,
        title: '选择商品', // 弹窗title
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          queryString: '', // 查询条件
          platformCityGoodsIds: [], // 平台区域商品ID
          platformId: 5,
          saleType: 3, // 售卖类型：1：常规；2：预售；3:长周期预售，4：及时达(查询对应售卖类型数据该字段必传，不传入择默认全部)
          categoryIdList: [],
          categoryId: '' // 商品分类ID
        },
        tableData: [],
        // 默认分类参数
        cateDefaultProps: {
          children: 'categoryVoList',
          label: 'categoryName',
          value: 'categoryId'
        },
        categoryList: [], // 商品分类id

        // 选中的商品
        checkedList: []
      }
    },
    components: {
      "goods-select-for-dialog": GoodsSelect
    },
    methods: {
      /**
       * [setDefault 设置基本选中]
       * @param {[type]} _val [description]
       */
      setDefault(_val) {
        if(_val) {
          this.title = _val.title
          this.checkedList = []
        }

        this.isShow = true
        // 获取三方平台分类
        this.getCategoryId()
        // 查询
        this.doSearchHandel(true)
        // 调用通用的SKU编码和商品名称筛选框
        setTimeout(() => {
          this.goodsSelectForDialogInit()
        }, 200)
      },
      /**
       * [getCategoryId 三方平台店内分类]
       * @return {[type]} [description]
       */
      getCategoryId() {
        this.$http.post(presaleGoodsMgt.getCategoryTree).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                if(item.categoryVoList && item.categoryVoList.length === 0) {
                  item.categoryVoList = null
                }
              }
              this.categoryList = res.data
            }else {
              this.categoryList = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(三方平台店内分类):", e.message)
        })
      },
      /**
       * [presaleStatusFormatter 格式化状态]
       * @return {[type]} [description]
       */
      presaleStatusFormatter(row, column, cellValue, index) {
        if(cellValue === 0) {
          return '已失效'
        }else if(cellValue === 1) {
          return '未开始'
        }else if(cellValue === 2) {
          return '进行中'
        }else if(cellValue === 3) {
          return '售罄'
        }
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel 查询列表接口]
       * @param  {[type]} _param [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(presaleGoodsMgt.selectPageGoodsList, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            if(res.data && res.data.result && res.data.result.length) {
              for(let item of res.data.result) {
                item.uniqueId = `${item.platformCityGoodsId}` + `${item.skuNo}`
              }
              this.tableData = res.data.result            
            }else {
              this.tableData = []
            }
            if(this.pageNum == 1) {
              this.total = res.data.total
            }

            this.defauleSelectFun()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArrayBykey(row.uniqueId, 'uniqueId', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('uniqueId', row.uniqueId, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.uniqueId] ? '' : hash[next.uniqueId] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {        
          // 全选
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 去重
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.uniqueId] ? '' : hash[next.uniqueId] = true && item.push(next)
            return item
          }, [])
        }else { 
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.从要传递的参数中去掉这些重复的
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'uniqueId')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [doConfirmHandel 点击导入按钮]
       * @return {[type]} [description]
       */
      doConfirmHandel() {
        let plateformCityGoodsIds = []
        for(let item of this.checkedList) {
          plateformCityGoodsIds.push(item.platformCityGoodsId)
        }
        let _params = {
          plateformCityGoodsIds: plateformCityGoodsIds,
          platformId: 5
        }
        this.isDisabled = true
        this.$http.post(presaleGoodsMgt.saveRecommend, _params).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("导入成功")
            this.$emit("importGoodsSuccess")
            this.isShow = false
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [defauleSelectFun 设置表格中的默认选中项]
       * @param    {[type]}                 row [description]
       * @return   {[type]}                     [description]
       */
      defauleSelectFun() {
        let rows = []
        for(let i of this.tableData) {
          for(let j of this.checkedList) {
            if(i.uniqueId == j.uniqueId) {
              rows.push(i)
            }
          }            
        }
        if(rows.length > 0) {
          this.$refs.multipleTable.clearSelection()
          setTimeout(() => {
            rows.forEach(rowItem => {
              this.$refs.multipleTable.toggleRowSelection(rowItem)
            });
          }, 100)
        } else {
          this.$refs.multipleTable.clearSelection()
        }
      },

      /**
       * [doCategorysChangeHandel 分类发生变化]
       * @return {[type]} [description]
       */
      doCategorysChangeHandel() {
        // 商品筛选组件初始化
        this.goodsSelectForDialogInit()

        this.doSearchHandel(true)
      },
      /**
       * [goodsSelectForDialogInit 商品筛选组件初始化]
       * @return {[type]} [description]
       */
      goodsSelectForDialogInit() {
        if(this.$refs.GoodsSelectForDialog) {
          let _val = {
            queryString: this.searchContent.queryString,
            categoryIdList: this.searchContent.categoryIdList,
            saleType: this.searchContent.saleType,
            platformId: this.searchContent.platformId,
            platformCityGoodsIds: this.searchContent.platformCityGoodsIds
          }
          this.$refs.GoodsSelectForDialog.setDefaultMesg(_val)
        }
      },
      /**
       * [DoSelectGoodsHandel 确认选择]
       */
      DoSelectGoodsHandel(_params) {
        let checkedList = _params.checkedList
        let queryString = _params.queryString
        let platformCityGoodsIds = []
        for(let item of checkedList) {
          platformCityGoodsIds.push(item.platformCityGoodsId)
        }
        this.searchContent.platformCityGoodsIds = platformCityGoodsIds
        this.searchContent.queryString = queryString
        this.doSearchHandel(true)
      }

    }
  }
</script>


