<style>
  .p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .img-w {
    width: 80px;
    height: 80px;
  }
  .img-w>img {
    width: 80px;
  }
  .p-m-c-r {
    padding: 0 10px;
  }
  .erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .a-s-p-i {
    display: inline-block;
  }

  .sort-num .el-button--mini{
    padding: 6px 5px;
  }
</style>

<template>
  <div>
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary" @click="imortGoods" size="mini" icon="el-icon-plus">导入商品</el-button>
      </div>
      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <el-form-item label="SKU编码/商品名称">
            <goods-select ref="GoodsSelect" @DoSelectGoodsHandel="DoSelectGoodsHandel"></goods-select>
          </el-form-item>
          <el-form-item label="导入开始时间">
            <el-date-picker
              class="search-input" 
              v-model="searchContent.startCreateTime"
              type="date"
              placeholder="导入开始时间"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              size="small"
              @change="doSearchHandel(true)">
            </el-date-picker>
          </el-form-item>

          <el-form-item label="导入结束时间">
            <el-date-picker
              class="search-input" 
              v-model="searchContent.endCreateTime"
              type="date"
              placeholder="导入结束时间"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              size="small"
              @change="doSearchHandel(true)">
            </el-date-picker>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <!-- 搜索结果 -->
    <div class="table-wraper">
      <el-table
        class="fixed-height"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column min-width="236px" label="商品信息">
          <template slot-scope="scope">
            <div class="p-m-w">
              <div class="img-w">
                <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                <img v-else src="/shop/static/img_error.png" alt="">
              </div>
              <div class="p-m-c-r">
                <div>{{scope.row.goodsName || '--'}}</div>
                <div class="erp-p-no">SKU编码：{{scope.row.goodsNO || '--'}}</div>
                <div class="erp-p-no">商品ID：{{scope.row.recommendGoodsId || '--'}}</div>
              </div>
            </div>
          </template>     
        </el-table-column>
        <el-table-column min-width="120px" prop="spec" label="规格"></el-table-column>
        <el-table-column min-width="100px" prop="price" label="原价"></el-table-column>
        <el-table-column min-width="100px" prop="presalePrice" label="预售价"></el-table-column>
        <el-table-column min-width="180px" label="预售时间">
          <template slot-scope="scope">
            <div>
              <p>{{scope.row.presaleStartDate}}-{{scope.row.presaleEndDate}}</p>
              <p>{{scope.row.presaleStartTime}}-{{scope.row.presaleEndTime}}</p>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="120px" prop="presaleStatus" label="状态" :formatter="presaleStatusFormatter"></el-table-column>
        <el-table-column label="排序" min-width="160px">
          <template slot-scope="scope">
            <div v-if="scope.row.showUpdate == false" class="sort-num">
              <el-button-group>
                <el-input v-model="scope.row.sortNum" size="mini" style="width: 80px;" @focus="modifySortNum(scope.$index, scope.row)"></el-input>
              </el-button-group>
            </div>
            <div v-else-if="scope.row.showUpdate == true" class="sort-num">
              <el-button-group>
                <el-input v-model="scope.row.updateSortNum" size="mini" style="width: 80px;" @blur="deleteSortNum(scope.$index, true)"></el-input>
              </el-button-group>
              <el-button-group>
                <el-button icon="el-icon-check" size="mini" style="width: 25px;" @click="sureUpdataSortNum(scope.row)"></el-button>
                <el-button icon="el-icon-close" size="mini" style="width: 25px;" @click="deleteSortNum(scope.$index)"></el-button>
              </el-button-group>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="160px" prop="createTime" label="导入时间"></el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="100px">
          <template slot-scope="scope" size="small">
            <div>
              <el-button @click="deleteItem(scope.row)" size="mini" icon="el-icon-delete">移除</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-show="tableData.length>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="[10, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <!-- 选择商品 -->
    <choose-goods ref="chooseGoods" @importGoodsSuccess="doSearchHandel(true)"></choose-goods>
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import ChooseGoods from "./ChooseGoods"
  import GoodsSelect from "@/components/Common/GoodsSelectByPid"
  const timeProductsFea = {
    // 商品推荐删除
    delRecommend: '/plateform/a/mall/recommend/delRecommend',
    // 商品推荐列表
    recommendList: '/plateform/a/mall/recommend/recommendList',
    // 区域商品选择-商品列表
    selectCityGoods: '/plateform/a/platformCityGoods/presale/selectCityGoods',
    // 商品推荐排序
    recommendSort: '/plateform/a/mall/recommend/recommendSort'
  }
  export default {
    data() {
      return {
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          queryString: '', // 查询条件
          platformId: 5,
          // 推荐类型：1：常规；2：预售；3:长周期预售，4：及时达
          recommendType: 3,

          platformCityGoodsIds: [], // 平台区域商品ID

          startCreateTime: '', // 创建日期开始
          endCreateTime: '', // 创建日期结束
        },
        tableData: [] // 表格中查询结果数据

      }
    },
    mounted() {
      this.doSearchHandel(true)

      // 商品筛选组件初始化
      this.goodsSelectInit()
    },
    components: {
      "choose-goods": ChooseGoods,
      "goods-select": GoodsSelect
    },
    methods: {
      /**
       * [imortGoods 导入商品]
       * @return {[type]} [description]
       */
      imortGoods() {
        let _params = {
          title: '选择商品'
        }
        this.$refs.chooseGoods.setDefault(_params)
      },
      /**
       * [presaleStatusFormatter 格式化状态]
       * @return {[type]} [description]
       */
      presaleStatusFormatter(row, column, cellValue, index) {
        if(cellValue === 0) {
          return '已失效'
        }else if(cellValue === 1) {
          return '未开始'
        }else if(cellValue === 2) {
          return '进行中'
        }else if(cellValue === 3) {
          return '售罄'
        }
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel 查询列表接口]
       * @param  {[type]} _param [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(timeProductsFea.recommendList, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            if(res.data && res.data.result && res.data.result.length) {
              for(let item of res.data.result) {
                item.showUpdate = false
                item.updateSortNum = item.sortNum
              }
              this.tableData = res.data.result            
            }else {
              this.tableData = []
            }

            if(this.pageNum == 1) {
              this.total = res.data.total
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [deleteItem 删除]
       * @param  {[type]} _row [行信息]
       * @return {[type]}      [description]
       */
      deleteItem(_row) {
        let _recommendIds = []
        let _id = _row.id
        _recommendIds.push(_id)
        let _param = {
          isDeleted: 1,
          recommendIds: _recommendIds
        }
        this.$confirm('您确定要移除所选商品吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(timeProductsFea.delRecommend, _param).then(res => {
            if(res.code === 0) {
              this.$message.success("移除成功")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [deleteSortNum 删除手动调价]
       * @return {[type]} [description]
       */
      deleteSortNum(index, val) {
        setTimeout(() => {
          this.tableData[index].updateSortNum = this.tableData[index].sortNum
          this.tableData[index].showUpdate = false
        }, 500)
      },
      /**
       * [modifySortNum 输入]
       * @return {[type]} [description]
       */
      modifySortNum(index, val) {
        if(val.showUpdate == true) {
          return
        }
        this.tableData[index].showUpdate = true
      },
      /**
       * [sureUpdataSortNum 确定更新排序]
       * @param  {[type]} _val [description]
       * @return {[type]}      [description]
       */
      sureUpdataSortNum(_val) {
        let numReg = /^[1-9]*[1-9][0-9]*$/
        let sortNum = parseInt(_val.updateSortNum)
        if(!sortNum && sortNum != 0) {
          this.$message("请输入序号")
          return 
        }else if(sortNum == 0) {
          sortNum = 0
        }else {
          if(!numReg.test(sortNum)) {
            this.$message("序号为数字")
          }
        } 

        let _params = {
          recommendId: _val.id,
          sortNum: sortNum
        }
        this.$http.post(timeProductsFea.recommendSort, _params).then(res => {
          if(res.code === 0) {
            this.$message.success("修改成功")
            this.doSearchHandel()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },

      /**
       * [goodsSelectInit 商品筛选组件初始化]
       * @return {[type]} [description]
       */
      goodsSelectInit() {
        if(this.$refs.GoodsSelect) {
          let _val = {
            queryString: this.searchContent.queryString,
            categoryIdList: this.searchContent.categoryIdList,
            saleType: this.searchContent.recommendType,
            platformId: this.searchContent.platformId,
            platformCityGoodsIds: this.searchContent.platformCityGoodsIds
          }
          this.$refs.GoodsSelect.setDefaultMesg(_val)
        }
      },
      /**
       * [DoSelectGoodsHandel 确认选择]
       */
      DoSelectGoodsHandel(_params) {
        let checkedList = _params.checkedList
        let queryString = _params.queryString
        let platformCityGoodsIds = []
        for(let item of checkedList) {
          platformCityGoodsIds.push(item.platformCityGoodsId)
        }
        this.searchContent.platformCityGoodsIds = platformCityGoodsIds
        this.searchContent.queryString = queryString
        this.doSearchHandel(true)
      }
    }
  }
</script>


